import { List } from 'antd-mobile';
import React from 'react';
import { Optimize, Schedule } from "@icon-park/react";
import Ying from "../../../Ying";
import { format, sections } from "../courseTable/components/Frame";
import dayjs from "dayjs";
import { getExtra, inTime } from "../thing";

const Plan: React.FC = () => {

    const now = dayjs();
    const rawPlan: {
        avatar: JSX.Element;
        name: string;
        description: string | JSX.Element;
        extra: string;
        timestamp: number;
    } [] = [];
    // 课程表
    Ying.courses.filter(course => {
        for (let schedule of course.schedules) {
            if (schedule.day === Ying.current.day && schedule.weeks.indexOf(Ying.current.week) !== -1) {
                return true;
            }
        }
        return false;
    }).forEach(course => {
        const schedule = course.schedules.filter(schedule => schedule.day === Ying.current.day)[0];
        rawPlan.push({
            avatar: <Schedule theme="outline" size="24" fill={course.color}/>,
            name: `${course.name}@${course.room}`,
            description: `课程表: 第${schedule.start}-${schedule.end}`,
            extra: `${sections[schedule.start][0].format(format)}-${sections[schedule.end][1].format(format)}`,
            timestamp: sections[schedule.start][0].unix()
        });
    });
    // 事项
    Ying.things.filter(thing =>
        inTime(thing)
    ).forEach(thing => {
        let timestamp = -Number.MAX_VALUE;
        if (thing.schedule?.time) {
            timestamp = thing.schedule.time[0].unix();
        }
        rawPlan.push({
            avatar: <Optimize theme="outline" size="24" fill="#333"/>,
            name: thing.title,
            description: thing.content,
            extra: getExtra(thing, false),
            timestamp: timestamp
        });
    });
    // 日历
    Ying.calender.filter(thing => {
        for (const date of thing.date) {
            if(dayjs(date, 'YYYY/MM/DD').isSame(now, 'day')) {
                return true;
            }
        }
        return false;
    }).forEach(thing => {
        let timestamp = -Number.MAX_VALUE;
        if (thing?.time) {
            timestamp = thing.time[0].unix();
        }
        rawPlan.push({
            avatar: <Optimize theme="outline" size="24" fill="#333"/>,
            name: thing.thing.title,
            description: thing.thing.content,
            extra: getExtra({
                schedule: {
                    time: thing.time,
                }
            }, false),
            timestamp: timestamp
        });
    });

    return (
        <>
            <>
                <strong
                    style={{
                        fontSize: 22
                    }}
                >
                    {now.format("MM/DD")}
                </strong>
            </>
            <List>
                {rawPlan.sort((x, y) =>
                    x.timestamp - y.timestamp
                ).map(plan => (
                    <List.Item
                        key={plan.name}
                        prefix={plan.avatar}
                        extra={plan.extra}
                        description={plan.description}
                    >
                        {plan.name}
                    </List.Item>
                ))}
            </List>
        </>
    );
};

export default Plan;
