import React, { useState } from 'react';
import { AutoCenter, Checkbox, List, Loading } from "antd-mobile";
import Ying from "../../../Ying";
import { format } from "../courseTable/components/Frame";
import dayjs from "dayjs";
import { Check } from '@icon-park/react';

const now = dayjs();
export const getExtra = (item: any, needDay = true) => {
    let result = '';

    // 日期
    if (needDay) {
        if (item.schedule?.day) {
            if (item.schedule.day.length === 1) {
                result += `${item.schedule.day[0].format('MM/DD')} `;
            } else if (item.schedule.day.length === 2) {
                result += `${item.schedule.day[0].format('MM/DD')}-${item.schedule.day[1].format('MM/DD')} `;
            }
        } else {
            result += '每天 ';
        }
    }
    // 时间
    if (item.schedule?.time) {
        if (item.schedule.time.length === 1) {
            result += `${item.schedule.time[0].format(format)}`;
        } else if (item.schedule.time.length === 2) {
            result += `${item.schedule.time[0].format(format)}-${item.schedule.time[1].format(format)}`;
        }
    }

    return result;
}
export const inTime = (item: any, exact = false) => {
    let result = false;
    // 是否是今天的事情
    if (item.schedule?.day) {
        if (item.schedule.day.length === 1) {
            result = item.schedule.day[0].isSame(now, 'day');
        } else if (item.schedule.day.length === 2) {
            result = (
                (item.schedule.day[0].isBefore(now, 'day') || item.schedule.day[0].isSame(now, 'day'))
                &&
                (item.schedule.day[1].isAfter(now, 'day') || item.schedule.day[1].isSame(now, 'day'))
            );
        }
    } else {
        result = true;
    }
    // 不是今天的事情，立刻返回
    if (!result) {
        return result;
    }
    // Todo: 是否在当前时间段内
    return result;
}

const Thing: React.FC = () => {

        const [display, setDisplay] = useState(false);

        return (
            <>
                <div>
                    &nbsp;
                    <Checkbox
                        style={{
                            float: 'right',
                            marginRight: 10.604
                        }}
                        checked={display}
                        onChange={() => setDisplay(!display)}
                    >
                        显示已完成
                    </Checkbox>
                </div>
                <List mode='card'>
                    {Ying.things.filter(item =>
                        display ? true : inTime(item)
                    ).map(item =>
                        <List.Item
                            title={item.title}
                            extra={getExtra(item)}
                            prefix={inTime(item) ? <Loading color='primary'/> :
                                <Check theme="outline" size="24" fill="#333"/>}
                        >
                            {item.content}
                        </List.Item>
                    )}
                </List>
                <AutoCenter>每月1日清理上月已完成事项</AutoCenter>
                <br/>
            </>
        );
    }
;

export default Thing;
